import React, { lazy, Suspense, useState, useEffect, useContext } from "react";
import { Button } from "../common/RowColStyle";
import { faSort } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { ResourcesListData } from "../../App";
const Cards = lazy(() => import("./cardView/Cards"));
const MapAutoComplete = lazy(() => import("../common/MapAutoComplete"));
const BadgeButtons = lazy(() => import("../common/BadgeButtons"));

const Home = () => {
  const ResourcesListDataContext = useContext(ResourcesListData);

  useEffect(() => {
    console.log("data", ResourcesListDataContext);
  });

  const [state, setState] = useState({
    lat: "",
    lng: "",
    address: "",
    fetchedUserLocation: false,
  });

  useEffect(() =>{
    console.log("i am superman")
    if(state.lat.length>0 || state.lng.length>0){
      ResourcesListDataContext.getNearbyResourcesList(state.lat,state.lng);
    }
  },[])

  const getCoordinates = (lat, lng, address = "") => {
    setState((prevState) => ({
      ...prevState,
      lat: lat,
      lng: lng,
      address: address,
      fetchedUserLocation: true,
    }));
    ResourcesListDataContext.getNearbyResourcesList(lat,lng);
  };

  return (
    <>
      <div>
        <div class="row">
          <div class="column">
            <Suspense fallback={<></>}>
              <MapAutoComplete getCoordinates={getCoordinates} />
            </Suspense>
          </div>
        </div>
        <div class="row">
          <div class="column">
            <Suspense fallback={<></>}>
              <BadgeButtons />
            </Suspense>
          </div>
        </div>
        <div class="row" style={{ float: "right" }}>
          <div class="column" style={{ marginBottom: "-1%" }}>
            <Suspense fallback={<></>}>
              <Button color="#2c3942">
                <FontAwesomeIcon icon={faSort} /> &nbsp;Sort
              </Button>
            </Suspense>
          </div>
        </div>
        <div class="row mb-0">
          <div class="column">
            <Suspense fallback={<></>}>
              <Cards mod={false} />
            </Suspense>
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
